<template><section><html><head></head><body><h1>EllipseGraphics</h1>
<p>The <code v-pre="">vc-graphics-ellipse</code> component is used to load an ellipse, the ellipse conforms to the curvature of the globe and can be placed on the surface or at altitude and can optionally be extruded into a volume. Need to be used as a subcomponent of <code v-pre="">vc-entity</code>.</p>
<h2>Example</h2>
<h3>Load EllipseGraphics with <code v-pre="">vc-graphics-ellipse</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity ref="entity1" :position="position1" :description="description" :ellipse.sync="ellipse1">
          <vc-graphics-ellipse :semi-minor-axis="300000.0" :semi-major-axis="300000.0" :height="200000.0" :material="material1" :outline="true"></vc-graphics-ellipse>
        </vc-entity>
        <vc-entity ref="entity2" :position="position2" :description="description" :ellipse.sync="ellipse2">
          <vc-graphics-ellipse :semi-minor-axis="250000.0" :semi-major-axis="400000.0" :material="material2"></vc-graphics-ellipse>
        </vc-entity>
        <vc-entity ref="entity3" :position="position3" :description="description" :ellipse.sync="ellipse3">
          <vc-graphics-ellipse :semi-minor-axis="150000.0" :semi-major-axis="300000.0" :extruded-height="200000.0" :rotation="rotation3" :material="material3" :outline="true"></vc-graphics-ellipse>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
