<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying the visibility of the ellipse.</td>
</tr>
<tr>
<td>semiMajorAxis</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The numeric Property specifying the semi-major axis.</td>
</tr>
<tr>
<td>semiMinorAxis</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The numeric Property specifying the semi-minor axis.</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the altitude of the ellipse relative to the ellipsoid surface.</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying what the height is relative to. <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A numeric Property specifying the altitude of the ellipse's extruded face relative to the ellipsoid surface.</td>
</tr>
<tr>
<td>extrudedHeightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying what the extrudedHeight is relative to. <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> A numeric property specifying the rotation of the ellipse counter-clockwise from north.</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number|Object</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> A numeric property specifying the rotation of the ellipse texture counter-clockwise from north.</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A numeric Property specifying the angular distance between points on the ellipse.</td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying whether the ellipse is filled with the provided material.</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre="">'white'</code></td>
<td><code v-pre="">optional</code> A Property specifying the material used to fill the ellipse.</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying whether the ellipse is outlined.</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">'black'</code></td>
<td><code v-pre="">optional</code> A Property specifying the Color of the outline.</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the width of the outline.</td>
</tr>
<tr>
<td>numberOfVerticalLines</td>
<td>Number</td>
<td><code v-pre="">16</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the number of vertical lines to draw along the perimeter for the outline.</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> An enum Property specifying whether the ellipse casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying at what distance from the camera that this ellipse will be displayed. <strong>structure: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>classificationType</td>
<td>Number</td>
<td><code v-pre="">2</code></td>
<td><code v-pre="">optional</code> An enum Property specifying whether this ellipse will classify terrain, 3D Tiles, or both when on the ground. <strong>TERRAIN: 0, CESIUM_3D_TILE: 1, BOTH: 2</strong></td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> A property specifying the zIndex of the Ellipse. Used for ordering ground geometry. Only has an effect if the ellipse is constant and neither height or exturdedHeight are specified.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/EllipseGraphics.html">EllipseGraphics</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>Triggers whenever a property or sub-property is changed or modified.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data () {
        return {
          description: 'Hello Vue Cesium',
          ellipse1: {},
          position1: { lng: 111.0, lat: 40.0, height: 150000.0 },
          material1: 'GREEN',

          ellipse2: {},
          position2: { lng: 103.0, lat: 40.0 },
          material2: undefined,

          ellipse3: {},
          position3: { lng: 95.0, lat: 40.0,  height: 100000.0 },
          rotation3: 0,
          material3: {}
        }
      },
      mounted() {
        Promise.all([this.$refs.entity1.createPromise, this.$refs.entity2.createPromise, this.$refs.entity3.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
          this.material2 = Cesium.Color.RED.withAlpha(0.5)

          this.material3 = Cesium.Color.BLUE.withAlpha(0.5)
          this.rotation3 = Cesium.Math.toRadians(45)
        }
      }
    }
  </script>